.container {
  text-align: center;
  background-color: #c9caca;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(196, 194, 194, 0.1);
  width: 100%;
}

.button {
  background-color: rgb(247, 247, 248);
  color: white;
  border: none;
  padding: 10px 15px;   
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  background-color: darkblue;
}
h1 {
  color: green;
}

/* General container */
.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 15px;
}

/* Labels */
label {
  font-size: 16px;
  font-weight: 600;
  color: #333;
  padding: 4px;
}

/* Inputs */
input {
  width: 11%;
  padding: 7.5px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

/* Input focus effect */
input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

#moduleName {
    font-size: 22px;
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

/* Button styling */
button {
  background-color: #007bff;
  color: white;
  font-size: 16px;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
}

button:hover {
  background-color: #0056b3;
}


#btn {
  margin-top: 7%; 
}

#mainHeader {
   font-size: 34px;
}